body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cardItem {
    width: 350px;
    height: 350px;

    background-color: #eee;
    border: #fff 10px solid;
    box-shadow: 0px 0px 5px 0px #ccc;
    position: absolute;

    -webkit-transition: all .3s cubic-bezier(0, .67, .45, 1.3);

    transition: all .3s cubic-bezier(0, .67, .45, 1.3);
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-transform-origin: 45% 0%;
            transform-origin: 45% 0%;

    display: flex;
    align-items: center;
    justify-content: center;
}

.cardItem h1 {
    font-size: 128px;
}
.cardContainer {
    width: 500px;
    height: 500px;

    /* outline: none; */
    /* touch-action: auto; */

    display: flex;
    align-items: center;
    justify-content: center;
}

.item1 {
    background-color: #ecce8e;
}
.item2 {
    background-color: #a9f0d1;
}
.item3 {
    background-color: #d3c1c3;
}
.item4 {
    background-color: #b5bfa1;
}
.item5 {
    background-color: #f9c80e;
}
.item6 {
    background-color: #a5d8ff;
}
.item7 {
    background-color: #333333;
}
/* container */
.instruction {
}

/* button */
.instruction__button {
    color: #b00020;
    font-weight: 700;

    display: inline-block;
    margin: auto 8px;
    margin-left: 10px;
    position: relative;
}

.instruction__button:before,
.instruction__button::after {
    content: '';
    height: 70%;
    width: 2px;
    background-color: #b00020;
    position: absolute;
}

.instruction__button:before {
    left: -5px;
    top: 3px;
}
.instruction__button::after {
    right: -3px;
    top: 3px;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
